'use client';

import { useState, useEffect } from 'react';
import { getStock, updateStock } from '@/actions/stock';

export default function StockManagePage() {
  const [basic, setBasic] = useState(0);
  const [premium, setPremium] = useState(0);
  const [special, setSpecial] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  const [isSaving, setIsSaving] = useState(false);
  const [message, setMessage] = useState('');

  useEffect(() => {
    getStock().then(data => {
      setBasic(data.basicStock);
      setPremium(data.premiumStock);
      setSpecial(data.specialStock);
      setIsLoading(false);
    });
  }, []);

  const handleSave = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSaving(true);
    setMessage('');
    
    const res = await updateStock(basic, premium, special);
    if (res.success) {
      setMessage('Stok berhasil diperbarui!');
    } else {
      setMessage(res.error || 'Gagal menyimpan stok');
    }
    setIsSaving(false);
    
    // Clear message after 3 seconds
    setTimeout(() => setMessage(''), 3000);
  };

  if (isLoading) {
    return <div className="animate-pulse">Memuat data stok...</div>;
  }

  return (
    <div className="max-w-2xl animate-in slide-in-from-bottom-4 duration-500">
      <h1 className="text-3xl font-bold mb-2">Manajemen Stok</h1>
      <p className="text-neutral-400 mb-8">Ubah ketersediaan stok paket gift skin.</p>

      {message && (
        <div className={`p-4 rounded-md mb-6 ${message.includes('berhasil') ? 'bg-emerald-500/10 text-emerald-400 border border-emerald-500/20' : 'bg-red-500/10 text-red-400 border border-red-500/20'}`}>
          {message}
        </div>
      )}

      <form onSubmit={handleSave} className="space-y-6 bg-neutral-900 border border-neutral-800 p-6 sm:p-8 rounded-xl shadow-xl">
        
        <div className="space-y-2">
          <label className="block text-sm font-medium text-neutral-300">Basic Gift Stock</label>
          <input 
            type="number" 
            min="0"
            value={basic}
            onChange={(e) => setBasic(parseInt(e.target.value) || 0)}
            className="w-full bg-neutral-950 border border-neutral-800 rounded-lg p-3.5 text-white focus:border-blue-500 focus:ring-1 focus:ring-blue-500 transition-all font-mono"
          />
          <p className="text-xs text-neutral-500">Stok untuk Skin Normal - Epic Shop</p>
        </div>

        <div className="space-y-2">
          <label className="block text-sm font-medium text-neutral-300">Premium Gift Stock</label>
          <input 
            type="number" 
            min="0"
            value={premium}
            onChange={(e) => setPremium(parseInt(e.target.value) || 0)}
            className="w-full bg-neutral-950 border border-neutral-800 rounded-lg p-3.5 text-white focus:border-blue-500 focus:ring-1 focus:ring-blue-500 transition-all font-mono"
          />
          <p className="text-xs text-neutral-500">Stok untuk Skin Collector / Limited</p>
        </div>

        <div className="space-y-2">
          <label className="block text-sm font-medium text-neutral-300">Special / Event Pass Stock</label>
          <input 
            type="number" 
            min="0"
            value={special}
            onChange={(e) => setSpecial(parseInt(e.target.value) || 0)}
            className="w-full bg-neutral-950 border border-neutral-800 rounded-lg p-3.5 text-white focus:border-blue-500 focus:ring-1 focus:ring-blue-500 transition-all font-mono"
          />
          <p className="text-xs text-neutral-500">Stok untuk Covenant Pass, Starlight, dll</p>
        </div>

        <div className="pt-6 border-t border-neutral-800">
          <button 
            type="submit" 
            disabled={isSaving}
            className="w-full bg-blue-500 hover:bg-blue-400 text-neutral-950 font-bold py-3.5 px-4 rounded-xl transition-all shadow-lg shadow-blue-500/20 disabled:opacity-50"
          >
            {isSaving ? 'Menyimpan...' : 'Simpan Perubahan'}
          </button>
        </div>
      </form>
    </div>
  );
}
