'use server';

import { prisma } from '@/lib/prisma';
import { revalidatePath } from 'next/cache';

export async function getStock() {
  try {
    const stock = await prisma.stockSetting.findFirst();
    if (!stock) {
      // Return defaults if none exists
      return { basicStock: 0, premiumStock: 0, specialStock: 0 };
    }
    return stock;
  } catch (error) {
    console.error("Failed to fetch stock:", error);
    return { basicStock: 0, premiumStock: 0, specialStock: 0 };
  }
}

export async function updateStock(basic: number, premium: number, special: number) {
  try {
    const existing = await prisma.stockSetting.findFirst();
    if (existing) {
      await prisma.stockSetting.update({
        where: { id: existing.id },
        data: {
          basicStock: basic,
          premiumStock: premium,
          specialStock: special,
        }
      });
    } else {
      await prisma.stockSetting.create({
        data: {
          basicStock: basic,
          premiumStock: premium,
          specialStock: special,
        }
      });
    }
    revalidatePath('/');
    revalidatePath('/admin/stock');
    return { success: true };
  } catch (error) {
    console.error("Failed to update stock:", error);
    return { success: false, error: "Gagal mengupdate stok." };
  }
}
